        }, 3000);
      }
    },

    // 
    async convertToValidWav(blob) {
      if (await this.isValidWavFile(blob)) {
        return blob;
      }

      try {
        const arrayBuffer = await new Response(blob).arrayBuffer();
        const audioContext = new (window.AudioContext || window.webkitAudioContext)({
          sampleRate: 16000
        });

        const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);
        const wavBuffer = this.convertToWav(audioBuffer);

        return new Blob([wavBuffer], { type: 'audio/wav' });
      } catch (error) {
        console.error(':', error);
        this.showStatus('', 'warning');
        return blob;
      }
    },

    async isValidWavFile(blob) {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onloadend = (e) => {
          if (e.target.readyState === FileReader.DONE) {
            const arrayBuffer = e.target.result;
            const uint8Array = new Uint8Array(arrayBuffer);

            if (uint8Array.length >= 4) {
              const riff = String.fromCharCode(uint8Array[0], uint8Array[1], uint8Array[2], uint8Array[3]);
              resolve(riff === 'RIFF');
            } else {
              resolve(false);
            }
          }
        };
        reader.readAsArrayBuffer(blob.slice(0, 4));
      });
    },

    convertToWav(audioBuffer) {
      const numberOfChannels = audioBuffer.numberOfChannels;
      const sampleRate = audioBuffer.sampleRate;
      const format = 1;
      const bitDepth = 16;

      let data = new Float32Array(audioBuffer.length);
      for (let i = 0; i < audioBuffer.length; i++) {
        let sum = 0;
        for (let c = 0; c < numberOfChannels; c++) {
          sum += audioBuffer.getChannelData(c)[i];
        }
        data[i] = sum / numberOfChannels;
      }

      const buffer = new ArrayBuffer(44 + data.length * 2);
      const view = new DataView(buffer);

      this.writeString(view, 0, 'RIFF');
      view.setUint32(4, 36 + data.length * 2, true);
      this.writeString(view, 8, 'WAVE');
      this.writeString(view, 12, 'fmt ');
      view.setUint32(16, 16, true);
      view.setUint16(20, format, true);
      view.setUint16(22, 1, true);
      view.setUint32(24, sampleRate, true);
      view.setUint32(28, sampleRate * 2, true);
      view.setUint16(32, 2, true);
      view.setUint16(34, bitDepth, true);
      this.writeString(view, 36, 'data');
      view.setUint32(40, data.length * 2, true);

      let index = 44;
      const volume = 1;
      for (let i = 0; i < data.length; i++) {
        const sample = Math.max(-1, Math.min(1, data[i] * volume));
        view.setInt16(index, sample < 0 ? sample * 0x8000 : sample * 0x7FFF, true);
        index += 2;
      }

      return buffer;
    },

    writeString(view, offset, string) {
      for (let i = 0; i < string.length; i++) {
        view.setUint8(offset + i, string.charCodeAt(i));
      }
    },

    // 
    async toggleCamera() {
      if (this.isCameraActive) {
        this.stopCamera();
        this.isCameraModalVisible = false;
      } else {
        this.isCameraModalVisible = true;
        await this.startCamera();
      }
    },

    async startCamera() {
      this.isLoadingCamera = true;
      this.cameraError = null;

      try {
        if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
          throw new Error('');
        }

        if (window.location.protocol !== 'https:' && window.location.hostname !== 'localhost') {
          throw new Error('HTTPS');
        }

        // 
        this.videoStream = await navigator.mediaDevices.getUserMedia({
          video: {
            width: { ideal: 1280 },
            height: { ideal: 720 }
          }
        });

        const videoElement = this.$refs.videoElement;
        if (videoElement) {
          videoElement.srcObject = this.videoStream;
          this.isCameraActive = true;
          this.showStatus('', 'success');
        }
      } catch (error) {
        console.error(':', error);
        this.cameraError = error.message;

        // 
        if (error.name === 'NotAllowedError') {
          this.cameraError = '';
        } else if (error.name === 'NotFoundError') {
          this.cameraError = '';
        } else if (error.name === 'NotReadableError') {
          this.cameraError = '';
        } else if (error.name === 'NotSupportedError') {
          this.cameraError = '';
        }

        this.showStatus(`: ${this.cameraError}`, 'error');
      } finally {
        this.isLoadingCamera = false;
      }
    },

    stopCamera() {
      if (this.videoStream) {
        this.videoStream.getTracks().forEach(track => track.stop());
        this.videoStream = null;
      }

      const videoElement = this.$refs.videoElement;
      if (videoElement) {
        videoElement.srcObject = null;
      }

      this.isCameraActive = false;
      this.isAnalyzingContinuously = false;

      if (this.continuousAnalysisTimer) {
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
      }

      if (this.detectionInterval) {
        clearInterval(this.detectionInterval);
        this.detectionInterval = null;
      }

      this.showStatus('', 'info');
    },

    // 
    async captureImage() {
      if (!this.isCameraActive || this.isProcessingImage) return;

      this.isProcessingImage = true;
      this.showStatus('...', 'info');

      try {
        const videoElement = this.$refs.videoElement;
        if (!videoElement) throw new Error('');

        // 
        const canvas = document.createElement('canvas');
        canvas.width = videoElement.videoWidth;
        canvas.height = videoElement.videoHeight;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(videoElement, 0, 0, canvas.width, canvas.height);

        // base64
        const base64Image = canvas.toDataURL('image/jpeg');

        // 
        const [expressionResults, poseResult] = await Promise.all([
          this.analyzeExpression(base64Image),
          this.analyzePose(base64Image)
        ]);

        // URL
        const imageUrl = URL.createObjectURL(await fetch(base64Image).then(res => res.blob()));

        // 
        this.addExpressionMessage(imageUrl, expressionResults, poseResult);

        this.showStatus('', 'success');
      } catch (error) {
        console.error(':', error);
        this.showStatus(`: ${error.message}`, 'error');
      } finally {
        this.isProcessingImage = false;
      }
    },

    // /
    startContinuousAnalysis() {
      if (this.isAnalyzingContinuously) {
        // 
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
        this.isAnalyzingContinuously = false;
        this.showStatus('', 'info');
      } else {
        // 2
        this.showStatus('...', 'info');
        this.isAnalyzingContinuously = true;

        // 
        this.captureImage();

        // 
        this.continuousAnalysisTimer = setInterval(() => {
          if (this.isAnalyzingContinuously) {
            this.captureImage();
          }
        }, 2000);
      }
    },

    // 
    async analyzeExpression(base64Image) {
      try {
        const response = await fetch(`${this.API_BASE_URL}/predict/image`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ base64: base64Image })
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`: ${response.status} - ${errorText.substring(0, 100)}`);
        }

        const data = await response.json();

        if (data.status === 'success' && data.predictions) {
          return data.predictions;
        } else {
          throw new Error(data.error || '');
        }
      } catch (error) {
        console.error(':', error);
        throw error;
      }
    },

    // 
    async analyzePose(base64Image) {
      try {
        // 
        const response = await fetch(`${this.API_BASE_URL}/process_pose_frame`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ base64: base64Image })
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`: ${response.status} - ${errorText.substring(0, 100)}`);
        }

        const data = await response.json();

        if (data.status === 'success' && data.result) {
          return {
            pose: data.result.pose,
            score: data.result.score,
            fps: data.result.fps || 0
          };
        } else {
          throw new Error(data.error || '');
        }
      } catch (error) {
        console.error(':', error);
        throw error;
      }
    },

    // 
    async analyzeComprehensive(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'expression') return;

      this.showStatus('...', 'info');

      try {
        // 
        let expressionsText = msg.expressionResults.map(exp =>
          `${exp.emotion} (${(exp.confidence * 100).toFixed(1)}%)`
        ).join('');

        let prompt = `:
: ${expressionsText}
: ${msg.poseResult.pose} (: ${(msg.poseResult.score * 100).toFixed(1)}%)

`;

        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
