    // 
    async openEvaluationReport() {
      this.isReportVisible = true;
      this.reportLoading = true;
      this.reportError = '';
      this.reportResponse = null; // 

      // 
      setTimeout(() => {
        this.generateReportFromStorage();
      }, 100);
