/*  */
.ability-details-container {
  margin-bottom: 20px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 8px;
}

.details-title {
  font-size: 16px;
  color: #334155;
  text-align: center;
  margin-bottom: 10px;
  font-weight: 500;
}

.details-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 10px;
  background: #fff;
  border-radius: 6px;
  font-size: 14px;
}

.indicator-name {
  color: #334155;
}

.score-value {
  font-weight: bold;
}

.excellent {
  color: #2ecc71;
}

.good {
  color: #f39c12;
}

.average {
  color: #3498db;
}

.poor {
  color: #e74c3c;
}

/*  */
.analysis-container {
  margin-bottom: 20px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 8px;
}

.analysis-title {
  font-size: 16px;
  color: #334155;
  margin-bottom: 15px;
  font-weight: 500;
}

.analysis-section {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px dashed #e2e8f0;
}

.analysis-section:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.analysis-section h4 {
  margin: 0 0 8px 0;
  font-size: 14px;
  color: #1e293b;
}

.analysis-section p {
  margin: 0;
  font-size: 13px;
  color: #475569;
  line-height: 1.5;
}

/*  */
.summary-container {
  margin-bottom: 20px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 8px;
}

.summary-title {
  font-size: 16px;
  color: #334155;
  margin-bottom: 15px;
  font-weight: 500;
}

.summary-content {
  background: #fff;
  padding: 15px;
  border-radius: 6px;
}

.summary-content p {
  margin: 0 0 10px 0;
  font-size: 13px;
  color: #475569;
  line-height: 1.5;
}

.summary-content h4 {
  margin: 15px 0 8px 0;
  font-size: 14px;
  color: #1e293b;
}

.summary-content ul {
  margin: 0;
  padding-left: 20px;
}

.summary-content li {
  font-size: 13px;
  color: #475569;
  margin-bottom: 5px;
  line-height: 1.5;
}

/*  */
.response-body-container {
  margin-top: 20px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 8px;
}

.response-body-title {
  font-size: 16px;
  color: #334155;
  margin-bottom: 10px;
  font-weight: 500;
}

.response-body-content {
  background: #fff;
  padding: 15px;
  border-radius: 6px;
  max-height: 200px;
  overflow-y: auto;
  font-family: monospace;
}

.response-body-content pre {
  margin: 0;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 12px;
  color: #334155;
}

/* Markdown */
.markdown-bubble {
  padding: 12px 15px;
  line-height: 1.8;
}

.markdown-render h1 {
  font-size: 1.5rem;
  margin: 1rem 0 0.5rem;
  color: #333;
}

.markdown-render h2 {
  font-size: 1.3rem;
  margin: 0.8rem 0 0.4rem;
  color: #444;
}

.markdown-render h3 {
  font-size: 1.1rem;
  margin: 0.6rem 0 0.3rem;
  color: #555;
}

.markdown-render ul {
  margin: 0.5rem 0;
  padding-left: 1.5rem;
}

.markdown-render li {
  margin: 0.3rem 0;
}

.markdown-render strong {
  font-weight: bold;
  color: #222;
}

.markdown-render em {
  color: #666;
  font-style: italic;
}

.markdown-render p {
  margin: 0.5rem 0;
}

/*  */
.markdown-render * {
  box-sizing: border-box;
}
.formatted-content {
  
  color: #111827; 
}


.formatted-content div[style*="color: rgba(107, 114, 128, 0.7)"] {
  color: rgba(107, 114, 128, 0.7) !important;
  background-color: rgba(243, 244, 246, 0.3) !important;
}
</style>
