<template>
    <!--  -->
    <div id="message" :style="{height:contentHeight}">
        <!--  -->
        <ul class="ui-list ui-list-function ui-border-tb">
            <!-- v-forfriendMessageList -->
            <li style="margin:0;" v-for="item in friendMessageList" :key="item.name">
                <van-swipe-cell style="width:100%;">
                    <van-cell 
                        center 
                        clickable 
                        :title="item.name" 
                        label="" 
                        :to="'/dialog/'+item.name"
                        @click="handleCellClick(item.name)"
                    >
                        <template #icon>
                            <van-image
                                round
                                width="50px"
                                height="50px"
                                :src="item.profile"
                                style="margin-right:10px;"/>
                        </template>
                        <!--  -->
                    </van-cell>
                    <template #right>
                        <van-button square type="primary" text="" style="height:100%;" />
                        <van-button square type="danger" text="" style="height:100%;" />
                    </template>
                </van-swipe-cell>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // 
            contentHeight: window.innerHeight - 100 + "px",
            // 
            // 
            friendMessageList: [
                {name: "", profile: require('../assets/images/profile_.jpg')},
                {name: "", profile: require('../assets/images/profile_02.jpg')},
                {name: "", profile: require('../assets/images/profile_.jpg')}
            ]
        };
    },
    methods: {
        // localStorage
        handleCellClick(name) {
            // ""
            const index = name.indexOf('');
            // 
            const career = index !== -1 ? name.substring(0, index) : name;
            // localStorage
            localStorage.setItem('career', career);
        }
    }
}
</script>

